html{
    height: 100%;
}


body{
    background-color: #2F3035;
    background-image: url(images/background.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}



.container{
    margin-left: auto;
    margin-right: auto;
    width: 1024px;

}



header{
    height: 118px;
    text-align: center;
    padding-top: 20px;
}


nav{
    background-color: rgba(0, 0, 0, 0.37);
    text-align: center;
    padding-top: 14px;
    padding-bottom: 14px;
}

nav a{
    text-decoration: none;
    font-size: 12px;
    padding-left: 10px;
    padding-right: 10px;

}

main{
    height: 400px;
    text-align: center;
    padding-top: 80px;
    padding-bottom: 30px;
    padding-left: 120px;
    padding-right: 120px;
}


main p{
    padding-left: 120px;
    padding-right: 120px;
    
}

footer{
    background-color: rgba(0, 0, 0, 0.61);
    padding-top: 60px;
    padding-bottom: 60px;
    text-align: center;
}

footer p{
    font-size: 10px;
}

h1{
    font-size: 84px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
}

p{
    font-size: 18px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
}

a{
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    /* text-decoration: none; */
}